import React from "react";
import { Box, Typography } from "@material-ui/core";
import { useStyles } from "./HeaderStyles";
import Navbar from "./Navbar";
import { Decorator } from "../common/commonComponent";
import ArrowDownwardRoundedIcon from "@material-ui/icons/ArrowDownwardRounded"
const HeaderComponent = () => {
  const classes = useStyles();
  return (
    <Box className={classes.handleWrapper}>
      <Navbar />
      <Box className={classes.Headercontainer}>
        <Typography variant="h3" component="h4" className={classes.headerTitle}>
          Hello I am Tarun ,|
        </Typography>
        <Typography variant="h3" component="h4" className={classes.headerDesc}>
          I create Websites ,
          <br />
           Based on Your Needs 
        </Typography>
        {Decorator({label:"About Me",withIcon:true,Icon: <ArrowDownwardRoundedIcon />})}
       
      </Box>
    </Box>
  );
};

export default HeaderComponent;
