import { Hidden , Button, Typography, AppBar, Toolbar, Box, IconButton } from "@material-ui/core";
import React from "react";
import { useStyles } from "./HeaderStyles";
import MenuOpenRoundedIcon from "@material-ui/icons/MenuOpenRounded"
const Navbar = () => {
  const classes = useStyles();
  const navlinks = [
    { label: "About", Id: "About" },
    { label: "Portfolio", Id: "Portfolio" },
    { label: "Contact", Id: "Contact" },
  ];
  return (
    <AppBar position="fixed" className={classes.navbar}>
      <Toolbar className={classes.toolbar}>
        <Typography variant="h5" component="h6" color="inherit">
          {" <GreatCoders /> "}
        </Typography>
        <Box component={Hidden} xsDown>
            <Box>
          {navlinks.map((item, i) => (
            <Button className={classes.navlinks} >{item.label}</Button>
          ))}
          </Box>
        </Box>
        <Box component={Hidden} smUp>
          <IconButton color="inherit"  onClick={()=>console.log("menu clicked")} >
            <MenuOpenRoundedIcon />
          </IconButton>
        </Box>
      </Toolbar>
    </AppBar>
  );
};

export default Navbar;
