import { useStyles } from "../HeaderComponent/HeaderStyles";
import { useStyles as BodyStyles } from "../BodyComponents/BodyStyles";
import { Box, Typography } from "@material-ui/core";


export const Decorator = ({ label, withIcon, Icon ,styles}) => {
  const classes = useStyles();
  return (
    <Box className={classes.decorator} style={styles} >
      <Typography variant="span" className={classes.decoratorText}>
        {label}
      </Typography>
      {withIcon ? (
        <Typography variant="span" className={classes.decoratorArrow}>
          {Icon}
        </Typography>
      ) : null}
    </Box>
  );
};
export const Divider = () => {
  const classes = BodyStyles();
  return <Typography variant="span" className={classes.divider}></Typography>;
};

export const RenderSectionHeading = ({ smallTxt, heading, alignCenter ,description}) => {
  const classes = BodyStyles();
return (

 <Box>
  {
    Decorator({ label: smallTxt, withIcon: false ,styles:alignCenter ? {width:"100px",margin:"10px auto"}:{}})
  }
  <Typography
    variant="h4"
    align={alignCenter ? "center" : "left "}
    className={classes.sectionHeading}
  >
    {heading}
  </Typography>
  {
    Divider()
  }
  <Typography variant="body1" component="h6" className={classes.sectionHeading}>
    {description}
  </Typography>
  </Box>
)
 
}
